import { Box, Collapse, Container, List, ListItemButton, ListItemIcon, ListItemText, Typography } from "@mui/material";
import React, { useState } from "react";

import ExpandLess from '@mui/icons-material/ExpandLess';
import ExpandMore from '@mui/icons-material/ExpandMore';
import { getLang } from "../../../helper/utils";

const Item = (props) => {
  const { title, text } = props;
  const [open, setOpen] = useState(false);
  const handleClick = () => setOpen(!open);
  return (
    <>
      <ListItemButton className="site__faq-item" onClick={handleClick}>
          <ListItemText primary={title} />
          {open ? <ExpandLess /> : <ExpandMore />}
      </ListItemButton>
      <Collapse in={open} timeout="auto" unmountOnExit style={{
        paddingLeft: '2em',
        paddingBottom: '2em'
      }}>
        <div dangerouslySetInnerHTML={{__html: text}}></div>
      </Collapse>
    </>
  )
}
const FAQs = (props) => {
	const language = getLang(); 
	const pageTexts = language.pages.faqs;

  return (
    <Container className="site__faq">
      <Typography variant="h3" align="center" >FAQs</Typography>

      {!!pageTexts && typeof pageTexts.items !== 'undefined' && pageTexts.items.length > 0 &&
      <List>
        {pageTexts.items.map((el, idx) => (
          <Item key={`faq-item-${idx}`} title={el.question} text={el.answer} />
        ))}
      </List>
      }
    </Container>
  )
}

export default FAQs;