import React, { useState } from "react";

import { 
  Grid, 
  Box,
  Button,
  Card,
  CardContent,
  TextField,
  Typography
} from "@mui/material";


import CheckSharpIcon from "@mui/icons-material/CheckSharp";
import CloseSharpIcon from "@mui/icons-material/CloseSharp";

import ImageHandler from "./ImageHandler";
import { __ } from "../../../helper/utils";

const PricingTableColumn = (props) => {
  const { id, column, image, imageType = 'default', price, callback } = props;
  const [email, setEmail] = useState(props?.email ?? '');

  const submitHandler = (e) => {
    e.preventDefault();

    callback({
      email: email,
      price_id: price.id
    });
  }

  const handleChange = (e) => {
    setEmail(e.target.value)
  }
  
  return (
    // Enterprise card is full width at sm breakpoint
    <Grid
      item
      key={column.title}
      xs
      sm={6}
      md={6}
      className="pricing_table__column"
    >
      <Card>
        <CardContent>
          <Box className="column__title">
            <div className="wrap">
              {!!column.sub_title && <Typography component="h6">{column.sub_title}</Typography>}
              <Typography component="h3">{column.title}</Typography>
              {!!price && 
                <Typography
                  id={`price-${id}`}
                  className="column__price"
                  component="h2"
                  variant="h3"
                  color="text.primary"
                  sx={{ fontFamily: "Rubik", fontWeight: 600 }}
                >
                  {price.value <= 0 ? __('Free') : `${price.value}${price.currency}`}
                </Typography>
              }
              {console.log('rambu', imageType)}
              {imageType === '3D' &&
                <div className="_3d-wrap">
                  <ImageHandler image={image} className="image--3d" />
                  <div className="space3d">
                    <div className="_3dbox">
                      <div className="_3dface _3dface--front">
                        <img src={image} alt="image" />
                      </div>

                      <div className="_3dface _3dface--top"></div>
                      <div className="_3dface _3dface--left"></div>
                    </div>
                  </div>
                </div>
              }
              {imageType !== '3D' &&
                <ImageHandler image={image} className="image--3d" />
              } 
            </div>
          </Box>
          <Grid>
            <form action="/" onSubmit={submitHandler} style={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "center",
              alignItems: "center"
            }}>
              {/* <p>Only send request when email is valid -</p> */}
              <Button
                id={`button-submit-${id}`}
                className="column__button"
                type="submit"
                fullWidth
                variant="outlined"
              >
                {__(`Select Plan`)}
              </Button>
            </form>
          </Grid>
          <div className="column__description">
            {column.description.tick.map((line) => (
              <div
                key={line}
                style={{ display: "flex", alignItems: "flex-start"  }}
              >
                <CheckSharpIcon style={{ fontSize: "1.25em", marginRight: '1em' }} color="success" />
                <Typography
                  component="div"
                  variant="subtitle1"
                  align="left"
                >
                  {line}
                </Typography>
              </div>
            ))}
            {column.description.cross.map((line) => (
              <div
                key={line}
                style={{ display: "flex", alignItems: "flex-start" }}
              >
                <CloseSharpIcon style={{ fontSize: "1.25em", marginRight: '1em' }} color="error" />
                <Typography
                  component="div"
                  variant="subtitle1"
                  align="left"
                >
                  {line}
                </Typography>
              </div>
            ))}
          </div>
          
        </CardContent>
      </Card>
    </Grid>
  )
}

export default PricingTableColumn;