import React, { useEffect } from "react";
import CssBaseline from "@mui/material/CssBaseline";
import Typography from "@mui/material/Typography";
// import Link from '@mui/material/Link';
import GlobalStyles from "@mui/material/GlobalStyles";
import Container from "@mui/material/Container";

import useHttp from "../../../hooks/use-Http";
import { getLang } from "../../../helper/utils";
import Logo from "../LandingPage/Logo";

const STRIPE_CANCEL_URL = process.env.REACT_APP_STRIPE_CANCEL_URL || "https://dev1.unikbase.dev/meveo/rest/cancelStripeSession";

const CancelPage = (props) => {
	const languages = getLang();
	const texts = languages.pages.cancel_page;
	let params;

	let ses_id = localStorage.getItem("session_id");
	if ( ses_id ) {
		params = localStorage.getItem(`_tpk_request_${ses_id}`);
		try {
			params = JSON.parse(params)
		} catch (e) {
			params = {}
		}
	}

	// Update return link
	let sub_title = texts.sub_title;
	sub_title = sub_title.replace('href="/"', `href="/?tpk_id=${params.tpk_id}&value=${params.value}&image=${encodeURIComponent(params.image)}"`);

	const {
		isLoading: isLoading,
		hasError: httpError,
		sendRequest: sendRequest,
		setIsLoading: setIsLoading,
		setHttpError: setHttpError,
	} = useHttp({ url: `${STRIPE_CANCEL_URL}`, method: "POST" });

	useEffect(() => {
		if (ses_id) {
			sendRequest({
				"sessionId": ses_id
			})
				.catch((error) => {
					setIsLoading(false);
					setHttpError(error.message);
				})
				.then((url) => {
					console.log(url);
				});
			console.log("CANCELED SUCCESSFULLY");
			//  localStorage.clear();
			ses_id = null;
		}
	}, ses_id);

	return (
		<React.Fragment>
			<GlobalStyles
				styles={{ ul: { margin: 0, padding: 0, listStyle: "none" } }}
			/>
			<CssBaseline />
			<Container
				component="main"
				sx={{ pr: "0.5rem", pl: "0.5rem", height: "60rem" }}
				xs={6}
				sm={12}
			>
				{/* Hero unit */}
				<Container
					disableGutters
					maxWidth="md"
					component="main"
					sx={{
						pb: 4,
						pr: "0.5rem",
						pl: "0.5rem",
						pt: "4rem",
						color: "#F3EFEA",
					}}
					xs={6}
					sm={12}
				>

					<div className="site__brand" style={{ marginBottom: '3em' }}>
						<a className="site__logo" href="https://unikbase.com/" title="Unikbase" target="_blank" rel="noreferrer">
							<Logo className="site__logo--image" />	
						</a>
					</div>
					<Typography
						component="h2"
						variant="h4"
						align="center"
						color="#F3EFEA"
						gutterBottom
						sx={{
							fontFamily: "Rubik",
							fontWeight: 600,
							mb: "1rem",
							pb: "0rem",
						}}
					>
						{texts.title}
					</Typography>
					<Typography
						variant="h5"
						align="center"
						color="#EA5123"
						component="p"
						sx={{ mb: "1rem", fontFamily: "Rubik", fontWeight: 400 }}
					>
            <div className="site__description" dangerouslySetInnerHTML={{__html: sub_title}}></div>
					</Typography>

					<Typography
						variant="h5"
						align="center"
						color="#EA5123"
						component="p"
						sx={{ mb: "1rem", fontFamily: "Rubik", fontWeight: 400 }}
					>
						<a href={`mailto:${texts.email}`} target="_blank" rel="noreferrer" title={`Email to ${texts.email}`}>{texts.email}</a>
					</Typography>

					<Typography
						variant="subtitle2"
						align="center"
						sm={6}
						sx={{ fontFamily: "Rubik", mb: "1rem", fontWeight: 300 }}
					>
						<div className="site__description" dangerouslySetInnerHTML={{__html: texts.description}}></div>
					</Typography>
				</Container>

				{/* End hero unit */}
			</Container>
		</React.Fragment>
	);
}

export default CancelPage;
