import React, { useEffect, useState } from "react";
import { 
  CssBaseline, 
  Grid,
  Typography,
  Container,
	Collapse,
} from "@mui/material";

import { getOfferId, getLang } from "../../../helper/utils";
import { prices, offers } from "../../../config/product";
import useInput from "../../../hooks/use-Input";
import useHttp from "../../../hooks/use-Http";
import SuccessCard from "../../UI/SuccessCard";
import PricingTableColumn from "./PricingTableColumn";

// import Link from '@mui/material/Link';
import { createTheme, ThemeProvider } from "@mui/material/styles";
import './LandingPage.scss';
import { useSearchParams } from "react-router-dom";
import Logo from "./Logo";
import FAQs from "./FAQ";
import Footer from "../../Layout/Footer";

const REDIRECT_URL = process.env.REACT_APP_REDIRECT_URL || "https://unikbase.com";
const STRIPE_PAYMENT_URL = process.env.REACT_APP_STRIPE_PAYMENT_URL || "https://dev1.unikbase.dev/meveo/rest/strcheckout";


const theme = createTheme({
	typography: {
		fontFamily: [
			'Rubik'
		]
	},
	status: {
		danger: "#e53e3e",
	},
	palette: {
		primary: {
			main: "#EA5123",
			darker: "#053e85",
		},
		neutral: {
			main: "#64748B",
			contrastText: "#fff",
		},
	},
	invalid: {
		backgroundColor: "#fff",
		transition: "300ms",
		display: "flex",
		justifyContent: "center",
		flexDirection: "column",
		"& p": {
			color: "rgb(218, 63, 63)",
			marginTop: "0",
			transition: "400ms",
			fontSize: "0.5rem",
			textAlign: "center",
			marginRight: "3rem",
		},
	},
});

const LandingPageContent = ( props ) => {
	const [offer, setOffer] = useState({});
	const [status, setStatus] = useState('idle');

	const [searchParams] = useSearchParams();
	const urlParams = new URLSearchParams(window.location.search);
	const language = getLang(); //Replace it later by func
	const pageTexts = language.pages.landing_page;

	let pricingTables = pageTexts.pricing_table || {};

	//Converting tpk_id to get price from table
	let tpk_id;

	tpk_id = urlParams.get("tpk_id");
	if ( !tpk_id ) tpk_id = searchParams.get('tpk_id');

	let image = urlParams.get("image");
	if ( !image ) image = searchParams.get('image');


  console.log( STRIPE_PAYMENT_URL );

	const {
		isLoading: isLoadingPost,
		hasError: httpPostError,
		sendRequest: sendPostRequest,
		setIsLoading: setIsLoadingPost,
		setHttpError: setHttpErrorPost,
	} = useHttp({ url: STRIPE_PAYMENT_URL, method: "POST" });
	// const {isLoading:isLoadingGet,hasError:httpGetError,sendRequest:sendGetRequest,setIsLoading:setIsLoadingGet,setHttpError:setHttpErrorGet} = useHttp({url:'https://checkout.stripe.com',method: 'GET'})

	useEffect(() => {
		if ( !tpk_id ) {
			window.location.replace(REDIRECT_URL);
			return;
		}
		
		let offerId = getOfferId(tpk_id);
		let currentOffer = offers.find((item) => item.id === offerId)
		console.log(currentOffer)
		setOffer(currentOffer.columns);
	}, []);

	////

	const submitHandler = (data) => {

		let payload = {
			email: data.email,
			tpk_id,
			value: data.price_id, //price_item,
		};
		setStatus('loading');
		sendPostRequest(payload)
      .catch((error) => {
        setIsLoadingPost(false);
        setHttpErrorPost(error.message);
      })
      .then((url) => {
        // console.log("URL",data)
        if (url) {
          const session_id = url
            .split("https://checkout.stripe.com/c/pay/")
            .join("");
          localStorage.setItem("session_id", session_id);
					localStorage.setItem(`_tpk_request_${session_id}`, JSON.stringify({
						...payload,
						image: image
					}));
          window.location.replace(url)
        }
      });
	};
	
	return (
  	<ThemeProvider theme={theme}>
			<CssBaseline />
			{!!(status === 'loading') && <div className="loading"></div>}
			<Container
				component="main"
				sx={{ pr: "0.5rem", pl: "0.5rem", pt: 2 }}
				xs={6}
				sm={12}
			>
				<Container
					disableGutters
					maxWidth="md"
					component="main"
					sx={{ pb: 4, pr: "0.5rem", pl: "0.5rem", color: "#F3EFEA" }}
					xs={6}
					sm={12}
				>
					<div className="site__brand">
						<a className="site__logo" href="https://unikbase.com/" title="Unikbase" target="_blank" rel="noreferrer">
							<Logo className="site__logo--image" />	
						</a>
					</div>
					<Typography
						component="h2"
						variant="h4"
						align="center"
						gutterBottom
						className="site__title"
					>
            {pageTexts.title}
					</Typography>
				</Container>

				{!!tpk_id &&
				<Container maxWidth="md" component="main" sx={{ padding: "0rem" }}>
					<Grid container spacing={0} justifyContent="center">
						{!!pricingTables.columns && pricingTables.columns.map((column, idx) => {
              let price = prices.find(el => el.id === offer[idx])

              return (
                <PricingTableColumn key={`offer-${idx}`} id={`column-${idx}`} price={price} image={image} column={column} callback={submitHandler} imageType={idx >= 1 ? '3D' : ''} />
              )
            })}
					</Grid>
				</Container>}

				<Container className="site__description">
					<Typography
						variant="h3"
						align="center"
					>
						{pageTexts.subTitle}
					</Typography>

          <div className="description__content" dangerouslySetInnerHTML={{__html: pageTexts.description}}></div>
				</Container>

				<FAQs />
				<Footer />
			</Container>

  	</ThemeProvider>
	);
}

export default LandingPageContent;
